<style lang="stylus">

$datetime-item-height = 38px;
$datetime-head-height = 43px;

.mask-datetime {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  z-index: 1500;
  bottom: 0;
  right: 0;
  left: 0;
  top: 0;
  -webkit-justify-content: center;
  -webkit-align-items: center;
  -webkit-box-align: center;
  -webkit-box-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  align-items: center;
  background-color: rgba(0, 0, 0, .4);
}

.m-datetime {
  position: fixed;
  bottom: -9999px;
  left: -9999px;
  width: 100%;
  z-index: 1501;
  background-color: #FFF;
  transform: translate(0, 100%);
  transition: transform .25s;
  touch-action: none;
  &.datetime-active {
    bottom: 0;
    left: 0;
    transform: translate(0, 0);
  }
}

.datetime-input {
  width: 100%;
}

.datetime-head {
  display: flex;
  justify-content: space-between;
  position: relative;
  background-color: #FBFBFB;
  border-bottom: 1px solid #f2f2f2;

  .datetime-title{
    height: $datetime-head-height;
    line-height: $datetime-head-height;
    color: #999;
  }
  > a {
    height: $datetime-head-height;
    padding: 0 30px;
    font-size: 15px;
    display: flex;
    align-items: center;
    color: #555;
  }
  > a:last-child {
    color: #ff6f5c;
  }
}

.datetime-content {
  display: flex;
  width: 100%;
  height: (7*$datetime-item-height)px;
  position: relative;
  overflow: hidden;
}

.datetime-item {
  flex: 1;
  height: 100%;
  position: relative;
  font-size: 16px;
}

.datetime-item-content {
  > span {
    display: block;
    width: 100%;
    height: $datetime-item-height;
    line-height: $datetime-item-height;
    text-align: center;
  }
}

.datetime-item-box {
  height: 100%;
}

.datetime-mask,
.datetime-indicator {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  pointer-events: none;
}

.datetime-mask {
  z-index: 3;
  transform: translateZ(0px);
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6)), linear-gradient(to top, rgba(255, 255, 255, 0.95), rgba(255, 255, 255, 0.6));
  background-position: top, bottom;
  background-size: 100% $datetime-item-height * 3;
  background-repeat: no-repeat;
}

.datetime-indicator {
  display: flex;
  z-index: 4;
  justify-content: center;
  flex-direction: column;
  > span {
    display: block;
    width: 100%;
    height: $datetime-item-height;
    position: relative;
  }
}
</style>
<template>
  <div @click.stop="open" class="datetime-input">
    <span v-if='isShowHolder' class="placeholder" :class="{'error':error}">
      {{placeholder}}
    </span>
    <span v-if='!isShowHolder'>
      <span v-if="type === 'custom'" :class="{'placeholder':value==-1, 'error':error }">
        {{showName}}
      </span>
      <span v-if="type !== 'custom'">
        {{value}}
      </span>
    </span>
    <slot></slot>
  </div>
</template>

<script>
/* eslint-disable no-multi-assign */
import Vue from 'vue';
import Utils from './utils';
import Datetime from './Datetime';
import Custom from './Custom';

export default {
  name: 'XPicker',
  data() {
    return {
      picker: null,
      currentValue: this.value,
      isShowHolder: true,
      showName: '',
    };
  },
  props: {
    default: {
      type: [String, Number],
    },
    title: {
      type: String,
    },
    readonly: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    type: {
      type: String,
      validator(value) {
        return ['datetime', 'date', 'time', 'custom'].indexOf(value) > -1;
      },
      default: 'datetime',
    },
    timeStep: {
      type: Number,
      default: 1
    },
    startYear: {
      validator(val) {
        return /^\d{4}|0$/.test(val);
      },
      default: 0,
    },
    endYear: {
      validator(val) {
        return /^\d{4}|0$/.test(val);
      },
      default: 0,
    },
    startDate: {
      type: String,
      validator(value) {
        if (!value) return true;
        return Utils.isDateTimeString(value);
      },
    },
    endDate: {
      type: String,
      validator(value) {
        if (!value) return true;
        return Utils.isDateTimeString(value);
      },
    },
    startHour: {
      validator(val) {
        return /^(0|[1-9]|1[0-9]|2[0-3])?$/.test(val);
      },
      default: 0,
    },
    endHour: {
      validator(val) {
        return /^([1-9]|1[0-9]|2[0-3])?$/.test(val);
      },
      default: 23,
    },
    startMinute: {
      validator(val) {
        return /^(0|[1-9]|1[0-9]|2[0-9]|3[0-9]|4[0-9]|5[0-9])?$/.test(val);
      },
      default: 0,
    },
    endMinute: {
      validator(val) {
        return /^([1-9]|1[0-9]|2[0-9]|3[0-9]|4[0-9]|5[0-9])?$/.test(val);
      },
      default: 59,
    },
    yearFormat: {
      type: String,
      default: '{value}年',
    },
    monthFormat: {
      type: String,
      default: '{value}月',
    },
    dayFormat: {
      type: String,
      default: '{value}日',
    },
    hourFormat: {
      type: String,
      default: '{value}时',
    },
    minuteFormat: {
      type: String,
      default: '{value}分',
    },
    placeholder: {
      type: String,
      default: '请选择时间',
    },
    error: Boolean,
    value: {
      type: [String, Array, Number]
    },
    selectData: {
      type: Array,
    },
    setValueFn: {
      type: Function,
      default: () => {}
    },
    step: {
      type: Number,
      default: 1,
      require: false,
    }
  },
  watch: {
    value(val) {
      if (this.currentValue !== val) {
        this.render();
      }
    },
    startDate() {
      this.render();
    },
    endDate() {
      this.render();
    },
  },
  methods: {
    open() {
      if (this.readonly || this.disabled) return;
      this.picker.open();
    },
    close() {
      this.picker.close();
    },
    removeElement() {
      if (this.picker && this.picker.$el) document.body.removeChild(this.picker.$el);
    },
    render() {
      this.removeElement();
      const Picker = this.type === 'custom' ? Vue.extend(Custom) : Vue.extend(Datetime);
      const props = this._props;
      props.parentEL = this.$el;
      this.picker = new Picker({
        el: document.createElement('div'),
        data: props,
      });
      document.body.appendChild(this.picker.$el);
      this.picker.$on('pickerConfirm', (value, show, name) => {
        this.showName = name || '';
        this.currentValue = value;
        this.isShowHolder = show;
        this.$emit('input', value);
      });
    },
  },
  mounted() {
    this.render();
  },
  beforeDestroy() {
    this.removeElement();
  },
};
</script>



